<template>
	<view>
		<uni-file-picker ref="files" :auto-upload="false" limit="5" file-mediatype="all" title="最多选择5个文件"
			@select="select" @progress="progress" @success="success" @fail="fail"></uni-file-picker>
		<view class="btn">
			<u-button @click="upload">上传文件</u-button>
		</view>
		
		<!-- <button @click="download">下载文件</button> -->
	</view>
</template>

<script>
	import {
		mapState,
	} from 'vuex'
	export default {
		data() {
			return {

			}
		},
		computed:{
			...mapState(['uid', 'currentRole', 'hasLogin', 'userInfo', 'hasAuth']),
		},
		methods: {
			upload() {
				this.$refs.files.upload()
			},
			download(){
				uniCloud.getTempFileURL({
				    fileList: ['https://vkceyugu.cdn.bspapp.com/VKCEYUGU-d0864766-02e5-4b10-8e08-72e0af293546/3c8aa450-a56d-451e-beb9-a93521fd28d8.xls']
				})
				.then(res => {console.log(res)});
			},
			select(e) {
				console.log('选择文件：', e)
				for(let item of e.tempFiles){
					item.cloudPath = item.name
					console.log('选择文件：', item.cloudPath)
				}
			},
			// 获取上传进度
			progress(e) {
				console.log('上传进度：', e)
			},

			// 上传成功
			success(e) {
				console.log('上传成功',e)
				// console.log('上传成功',e.fileI)
				var time = new Date().getTime()
				const db = uniCloud.database()
				for(let item of e.tempFiles){
					db.collection('class-file').add({
						uploader_id : this.uid,
						upload_time:time,
						class_id:"607a77c5a23da8000194f7c7",
						file_detail:item
					}).then((res=>{
						console.log(res)
					}))
				}
			},

			// 上传失败
			fail(e) {
				console.log('上传失败：', e)
			}
		}
	}
</script>

